import React, { useState, useEffect } from 'react';
import { Card, Form, Row, Input, message } from 'antd';
import { requiredRule } from 'utils/validateRules';
import { BtnSave, BtnCancel } from 'components/common/button';
import { SelectAjax, DatePickerString } from 'components/common';
import { getTrainTypes } from 'services/trainType';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { getQueryParams } from 'utils/urlUtil';
import {
  getRegisterSessionById,
  createRegisterSession,
  updateRegisterSession,
} from 'services/registerSession';
import moment from 'moment';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    trainTypeId: '',
    yearEduId: '',
    semesterId: '',
    sessionNo: '',
    startDate: '',
    endDate: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

const RegisterSessionForm = props => {
  const [id, setId] = useState();
  const [initialValue, setInitialValue] = useState({});
  const [loading, setLoading] = useState(false);

  const getDataById = async pathId => {
    const res = await getRegisterSessionById(pathId);
    setInitialValue(handleInitialValue(res.data));
  };

  useEffect(() => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      setId(queryParamId);
      getDataById(queryParamId);
    }
  }, []);

  const saveData = async values => {
    try {
      if (!id) {
        await createRegisterSession(values);
        message.success('Tạo mới thành công');
      } else {
        await updateRegisterSession({ id, ...values });
        message.success('Cập nhật thành công');
      }
      props.history.goBack();
    } catch (err) {
      console.log(err);
    }
  };

  const handleSubmit = e => {
    e.preventDefault();
    props.form.validateFields(async (err, values) => {
      if (err) {
        return;
      }

      // validate if end date is before start date
      const startDate = moment(values.startDate);
      const endDate = moment(values.endDate);

      if (endDate.isBefore(startDate)) {
        message.error('Không được chọn ngày kết thúc trước ngày bắt đầu');
        return;
      }

      setLoading(true);
      await saveData(values);
      setLoading(false);
    });
  };

  const currentDate = moment();

  const { getFieldDecorator } = props.form;
  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 5 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 },
    },
    colon: false,
  };

  return (
    <Card title="Thêm mới đợt đăng ký">
      <Row gutter={24}>
        <Form onSubmit={handleSubmit}>
          <Form.Item {...formItemLayout} label="Hệ" required>
            {getFieldDecorator('trainTypeId', {
              initialValue: initialValue.trainTypeId,
              rules: [requiredRule],
            })(<SelectAjax service={getTrainTypes} />)}
          </Form.Item>
          <Form.Item {...formItemLayout} label="Năm học" required>
            {getFieldDecorator('yearEduId', {
              initialValue: initialValue.yearEduId,
              rules: [requiredRule],
            })(<SelectAjax service={getYearEducations} typeValue="int" />)}
          </Form.Item>
          <Form.Item {...formItemLayout} label="Học kì" required>
            {getFieldDecorator('semesterId', {
              initialValue: initialValue.semesterId,
              rules: [requiredRule],
            })(<SelectAjax service={getSemesters} typeValue="int" />)}
          </Form.Item>
          <Form.Item {...formItemLayout} label="Đợt đăng ký" required>
            {getFieldDecorator('sessionNo', {
              initialValue: initialValue.sessionNo,
              rules: [requiredRule],
            })(<Input />)}
          </Form.Item>
          <Form.Item {...formItemLayout} label="Ngày bắt đầu" required>
            {getFieldDecorator('startDate', {
              initialValue: initialValue.startDate || currentDate,
              rules: [requiredRule],
            })(<DatePickerString placeholder="Chọn ngày" />)}
          </Form.Item>
          <Form.Item {...formItemLayout} label="Ngày kết thúc" required>
            {getFieldDecorator('endDate', {
              initialValue: initialValue.endDate || currentDate,
              rules: [requiredRule],
            })(<DatePickerString placeholder="Chọn ngày" />)}
          </Form.Item>
          <Form.Item {...formItemLayout} label=" ">
            <BtnSave htmlType="submit" loading={loading} />
            <BtnCancel onClick={() => props.history.goBack()} />
          </Form.Item>
        </Form>
      </Row>
    </Card>
  );
};

export default Form.create()(RegisterSessionForm);



